'use client'
import React, { useState } from 'react'
import Navbar from "@/components/Navbar"
import { useForm } from 'react-hook-form'
import '../page.css'
import { createProduct } from './action'
// import { GoogleGenerativeAI } from '@google/generative-ai'
// import { POST } from '../api/generate-description/route'
import { OpenAI } from 'openai'
import { POST } from '../api/generate-description/route'

export default function App() {
  const [prod,setProd]=useState("")
  const [loadingDesc, setLoadingDesc] = useState(false);

  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors },
  } = useForm();


 const autoGenerate = async () => {
    const name = watch('name');
    if (!name) return;

    try {
      setLoadingDesc(true);
      const res = await fetch('/api/generate-description', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ name }),
      });
      const { description, error } = await res.json();

      if (res.ok && description) setValue('description', description);
      else {
        console.error('API error:', error);
        alert('Failed to generate description.');
      }
    } catch (err) {
      console.error(err);
      alert('Failed to generate description.');
    } finally {
      setLoadingDesc(false);
    }
  };

    const onSubmit = async (data) => {
    const formData = new FormData();
    formData.append("name", data.name);
    formData.append("description", data.description);
    formData.append("img", data.img);
    formData.append("price", data.price);

    try {
      await createProduct(formData);   // writes to Prisma on the server
      reset();                         // clear the inputs
      alert("Product added successfully!");   // toast/alert of your choice
    } catch (err) {
      console.error(err);
      alert("Something went wrong – check the console.");
    }
  };

  return (
    <div>
      <Navbar />
      <h1 className="heading">Add A Product</h1>
    <div className="Forms">    
      <form className="form-card" onSubmit={handleSubmit(onSubmit)}>
      
    <div className="fields">
      <h3>Product Name</h3>
      <label className="form-label">

        <input
          className="form-input"
          {...register("name", { required: true })}
          placeholder="Cosmetic,Clothing,Food etc."
        />
      </label>
      <br></br>
      <h3>Enter Product Description</h3>
      <label className="form-label">
        <input
          className="form-input"
          {...register("description", { required: true })}
          placeholder="Enter a short description of the product"
        />
        {errors.description && <span className="error">Description is required</span>}
      </label>
{/* 
      <button
              type="button"
              className="generate-btn"
              onClick={() => autoGenerate(watch('prompt') || watch('name'))}
              disabled={loadingDesc}
            >
              {loadingDesc ? 'Generating…' : 'Auto‑Generate Description'}
            </button> */}

      <br></br>
      <h3>Image URL</h3>
      <label className="form-label">
        <input
          className="form-input"
          {...register("img", {required:true})}
          placeholder="Product Image URL"
        />
        <br></br>
        <h3>Price of the Product</h3>
        <label className="form-label">
        <input 
          className="form-input"
          {...register("price",{required:true})}
          placeholder="Price"
        />
      </label>
        
      {/* <h3>Gemini Prompt (optional)</h3>
            <label className="form-label">
              <textarea
                rows={3}
                className="form-input"
                {...register('prompt')}
                placeholder="Tell Gemini the tone, keywords, or extra context you want"
              />
            </label>
s */}
           {/* GENERATE BUTTON */}

           <br></br>
          <button
            type="button"
            className="generate-btn"
            onClick={autoGenerate}
            disabled={loadingDesc}
          >
            {loadingDesc ? 'Generating…' : 'Auto‑Generate Description'}
          </button>

      </label>
        <br></br>
        <div className="submitbutton">
      <button type="submit">
        Add Product
      </button>
      </div>
      </div>
    </form>
    </div>

    </div>
  )
}
